import React, {Component, Fragment} from "react";
import PropTypes from "prop-types";
import styled, {injectGlobal} from 'styled-components';
import axios from "axios";

injectGlobal`
    * {
      margin: 0;
      padding: 0;
    }
    
    @font-face {
      src: url("/fonts/Montserrat-Bold.ttf");
      font-family: 'Montserrat';
      font-weight: 600;
    }
    @font-face {
      src: url("/fonts/Montserrat-Regular.ttf");
      font-family: 'Montserrat';
      font-weight: 400;
    }
    
    @font-face {
      src: url("/fonts/Chivo-Regular.ttf");
      font-family: 'Chivo';
      font-weight: 400;
    }
`;


const CacheItemWrapper = styled.div`
    width: 200px;
    height: 50px;
    font-size: 18px;
    font-weight: 600;
    bacground-color: #aaa;
`;


export default class App extends Component {

    async componentWillMount() {
        const caches = await axios.get("https://api-cache.herokuapp.com/cache/");
        this.setState({
            caches: caches.data.data
        })
    }

    state = {
        caches: []
    };

    render() {
        return (
            <div>
                <h2>All cache items:</h2>
                {this.state.caches.map((cacheItem, index) => {
                    return (
                        <CacheItemWrapper key={index}>
                            Key: {cacheItem.key} <br/> Value: {cacheItem.value}
                        </CacheItemWrapper>
                    )
                })}
            </div>
        )

    }
}


